<template>
  <div>
    <v-toolbar flat class="primary">
      <v-spacer></v-spacer>
      <v-img
        :src="
          mode
            ? require('../../assets/image/switch_list_btn.svg')
            : require('../../assets/image/switch_node_btn.svg')
        "
        max-height="60"
        max-width="120"
        class="switch_mode_image"
        @click="mode = !mode"
      ></v-img>
    </v-toolbar>
    <gateway-list :gatewayinfo="gatewayinfo" v-if="mode" />
    <gateway-node :gatewayinfo="gatewayinfo" v-else />
  </div>
</template>
<script>
import GatewayList from "./GatewayList.vue";
import GatewayNode from "./GatewayNode.vue";
export default {
  props: ["gateway_data"],
  watch: {
    gateway_data() {
      this.onBandNumHandler(this.gateway_data);
    },
  },
  components: {
    GatewayList,
    GatewayNode,
  },
  created() {},
  async mounted() {
    this.$session.load = true;
    if (this.$session.user != null) {
      await this.getGatewayList();
      await this.getGatewayState();

      this.onBandNumHandler(this.gateway_data);
    }
    this.$session.load = false;
  },
  methods: {
    onBandNumHandler(data) {
      if (this.$session.showConsole) console.log(data);
      for (var i = 0; i < data.length; i++) {
        var index = this.getBandNum(this.gatewayinfo, data[i].panid);
        if (index !== -1) {
          this.gatewayinfo[index].cbandnum = data[i].bandnum;
          this.gatewayinfo[index].connect_state = data[i].connectstate;
        }
      }
    },
    async getGatewayState() {
      try {
        for (var gw in this.gatewayinfo) {
          this.$session.socket.emit("gwcheck", this.gatewayinfo[gw].pid);
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    getBandNum(gwList, panid) {
      var index = gwList
        .map(function(g) {
          return g["pid"];
        })
        .indexOf(panid);
      return index;
    },
    async getGatewayList() {
      try {
        var gateway = await this.$http.post("users/gwlist", {
          uid: this.$session.user.id,
          permission: this.$session.user.permission,
        });

        gateway = gateway.data.data;

        for (var g = 0; g < gateway.length; g++) {
          var cbandnum = 0;
          var connect_state = false;
          var index = this.getBandNum(this.gateway_data, gateway[g].pid);
          if (index !== -1) {
            cbandnum = this.gateway_data[index].bandnum;
            connect_state = this.gateway_data[index].connectstate;
          }
          var bandnum = await this.$http.get(
            "gatewaysbands/num/" + gateway[g].id
          );

          bandnum = bandnum.data.data;

          this.gatewayinfo.push({
            id: gateway[g].id,
            pid: gateway[g].pid,
            alias: gateway[g].alias,
            created: gateway[g].created,
            ip: gateway[g].ip,
            disconnect_time: gateway[g].disconnect_time,
            connect_time: gateway[g].connect_time,
            connect_state: connect_state,
            bandnum: bandnum,
            cbandnum: cbandnum,
            num: g + 1,
          });
        }
      } catch {
        this.gatewayinfo = [];
      }
    },
  },
  data: () => ({
    restapi: false,
    mode: true,
    gatewayinfo: [],
  }),
};
</script>
