        sortable: true,
        value: 'bandvalue.bandData.active',
      },
      {
        text: 'ID',
        value: 'num',
      },
      { text: '밴드ID', value: 'bid' },
      { text: '관계', value: 'alias' },
      { text: '이름', value: 'name' },
