<template>
  <v-container fluid>
    <v-row class="v-row-image text-center" style="text-align: center">
      <v-col class="text-center">
        <v-img
          style="display: block; margin: 0px auto;"
          :src="require('../assets/image/well-O.svg')"
          :width="180"
        />
      </v-col>
    </v-row>
    <v-row class="mt-10">
      <v-col>
        <v-text-field
          v-model="form.username"
          label="Username"
          filled
          outlined
          class="login_input"
        ></v-text-field>
        <v-text-field
          v-model="form.password"
          label="Password"
          filled
          outlined
          type="password"
          class="login_input"
        >
        </v-text-field>
        <v-alert v-if="!!message" type="error" class="login_input">
          {{ message }}
        </v-alert>
        <v-btn
          depressed
          justify-center
          class="secondary login_input"
          :loading="loading"
          @click="submit()"
          >sign in!</v-btn
        >

        <div class="text-normal">Forgot password</div>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    form: {
      username: "",
      password: "",
    },
    loading: false,
    message: null,
  }),
  computed: {},
  methods: {
    async submit() {
      this.loading = true;
      try {
        let { data } = await this.$http.post("login", this.form);
        if (!data.status)
          return (this.message = this.$vuetify.lang.t(
            "$vuetify.efwb.loginfailcode." + data.reason
          ));
        this.$session.setSession(data.user);
      } catch (error) {
        this.message = error.message;
      } finally {
        this.loading = false;
      }
    },
    clear() {
      this.name = "";
      this.password = "";
      this.select = null;
      this.checkbox = false;
    },
    async getGatewayState() {
      try {
        this.$session.socket.emit("message", 0);
      } catch (ex) {
        console.log(ex);
      }
    },
  },
  created() {},
};
</script>
<style lang="scss" scoped>
.text-normal {
  text-align: center;
  color: black;
}
.v-row-image {
  background-color: #43e396;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 30vh;
  align-content: center;
  justify-content: center;
  text-align: center;
}
@media screen and (max-width: 576px) {
  .login_input {
    left: 50%;
    transform: translateX(-50%);
    width: 90%;
  }
}
@media screen and (min-width: 577px) {
  .login_input {
    left: 50%;
    transform: translateX(-50%);
    width: 550px;
  }
}
</style>
