<template>
  <div>
    <v-container class="">
      <v-row align="center" justify="space-between">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }" class="">
            <v-btn
              :items="typeItems"
              outlined
              v-bind="attrs"
              v-on="on"
              class="mr-n16 content-text-style history-button-style "
            >
              {{ typeItems[typeclick] }}
              <v-icon right dark> mdi-menu-down </v-icon></v-btn
            >
          </template>
          <v-list>
            <v-list-item
              link
              v-for="(item, i) in typeItems"
              :key="i"
              @click="typeClick(i)"
            >
              <v-list-item-title>{{ item }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-row>
      <v-row align="center" justify="center" class="">
        <!-- <v-btn class="mr-4" icon><v-icon>mdi-chevron-left </v-icon></v-btn> -->
        <v-icon>mdi-calendar</v-icon>
        <v-menu
          ref="menu1"
          v-model="menu1"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          min-width="200"
        >
          <template v-slot:activator="{ on, attrs }" class="">
            <v-card-title
              v-text="dateFormatted"
              prepend-icon=""
              v-bind="attrs"
              v-on="on"
              class="detail-text-style "
            ></v-card-title>
          </template>
          <v-date-picker
            v-model="date"
            no-title
            :max="
              new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
                .toISOString()
                .substr(0, 10)
            "
            :min="minDate"
            @input="menu1 = false"
          ></v-date-picker>
        </v-menu>

        <!-- <v-btn class="ml-1" icon><v-icon>mdi-chevron-right </v-icon></v-btn> -->
      </v-row>
      <v-row align="center" justify="center">
        <apexchart
          class="history-chart-style"
          :type="graphType"
          :options="options"
          :series="slickSeries[slickclick]"
        ></apexchart
      ></v-row>
      <v-row>
        <v-col>
          <VueSlickCarousel v-bind="settings" style="color: black">
            <div v-if="slickSeries.length === 0">데이터 없음</div>
            <div
              align="center"
              v-for="(g, i) in slickSeries"
              :key="i"
              @click="slickCheck(i)"
            >
              <div
                class="mx-2"
                :class="slickClick[i].check ? 'lightgrey' : 'white'"
              >
                <v-row justify="center">
                  <v-col cols="6">
                    <strong
                      class="title"
                      v-text="
                        g.length > 1
                          ? typeclick === 0
                            ? g[0].name + '/' + g[1].name
                            : stepTitle[i]
                          : g[0].name
                      "
                    ></strong> </v-col
                ></v-row>
                <v-row align="center" justify="center">
                  <apexchart
                    :type="slickGraphType(g[0].name)"
                    :options="slickOptions"
                    :series="g"
                  ></apexchart>
                </v-row>
              </div>
            </div> </VueSlickCarousel
        ></v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
export default {
  props: ["bandinfo"],
  components: { VueSlickCarousel },
  create() {
    this.typeclick = 0;
  },
  async mounted() {
    this.dateFormatted = this.formatDate(this.date);
  },
  data(vm) {
    return {
      restapi: false,
      typeItems: ["전체", "심박수", "산소포화도", "활동"],
      menu1: false,
      minDate: null,
      dateList: [],
      settings: {
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 3,
      },
      graphType: "line",
      typeclick: 0,
      slickclick: 0,
      slickClick: [
        { check: true },
        { check: false },
        { check: false },
        { check: false },
        { check: false },
      ],
      options: {
        chart: {
          id: "vuechart-example",
          stacked: true,
        },
      },
      slickOptions: {
        chart: {
          id: "vuechart-example",
          stacked: true,
          toolbar: {
            show: false,
          },
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          width: 2,
        },

        xaxis: {
          labels: {
            show: false,
          },
        },
        yaxis: {
          show: false,
        },
        grid: {
          xaxis: {
            lines: {
              show: false,
            },
          },
          yaxis: {
            lines: {
              show: false,
            },
          },
        },
      },
      chartOptions: {
        chart: {
          height: 350,
          stacked: true,
        },
      },
      stepTitle: [],
      series: [],
      slickSeries: [],
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      dateFormatted: vm.formatDate(
        new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10)
      ),
    };
  },
  methods: {
    formatDate(date) {
      this.$session.load = true;
      if (!date) return null;
      if (this.typeclick != 0) {
        this.rangeDate(date);

        return [this.dateList[0], this.dateList[this.dateList.length - 1]].join(
          " ~ "
        );
      } else {
        this.dateList = [];
        this.dateList.push(date);

        this.changeGraph(this.dateList);
        return date;
      }
    },
    async changeGraph(dateList) {
      this.slickSeries = [];
      var date = new Date();
      var result = await this.$http.post("sensordata/oneday", {
        bid: this.bandinfo.id,
        date: dateList,
      });
      var date2 = new Date();
      if (this.$session.showConsole) console.log((date2 - date) / 1000);
      result = result.data.data;
      var seri = [];
      if (result != null) {
        this.$nextTick(() => {
          seri.push({ name: "심박수", data: result[0] });
          this.slickSeries.push(seri);
          seri = [];
          seri.push({ name: "산소포화도", data: result[1] });
          this.slickSeries.push(seri);
          seri = [];
          seri.push({ name: "걸음수", data: result[2] });
          seri.push({ name: "뜀박수", data: result[3] });
          this.slickSeries.push(seri);
        });
      }
      this.$session.load = false;
    },
    async rangeDate(val) {
      var computeDate = new Date(val);
      var today = this.$date.changeDateNoneFormat(new Date());
      this.dateList = [];
      var temp = new Date(computeDate);
      temp.setDate(temp.getDate() - computeDate.getDay());

      for (var i = 0; i < 7; i++) {
        if (today - this.$date.changeDateNoneFormat(temp) >= 0) {
          this.dateList.push(this.$date.changeDateFormat(temp));
        }
        temp.setDate(temp.getDate() + 1);
      }
      var result = [];
      var seri = [];
      var a = 0;

      switch (this.typeclick) {
        case 1:
          this.slickSeries = [];
          result = await this.$http.post("sensordata/range", {
            bid: this.bandinfo.id,
            dataname: "hr",
            days: this.dateList,
          });

          result = result.data.data;

          if (result.length != 0) {
            for (a = 0; a < result.length; a++) {
              seri = [];
              seri.push({ name: result[a].date, data: result[a].data });
              this.slickSeries.push(seri);
            }
          }
          break;
        case 2:
          this.slickSeries = [];
          result = await this.$http.post("sensordata/range", {
            bid: this.bandinfo.id,
            dataname: "spo2",
            days: this.dateList,
          });

          result = result.data.data;

          if (result.length != 0) {
            for (a = 0; a < result.length; a++) {
              seri = [];
              seri.push({ name: result[a].date, data: result[a].data });
              this.slickSeries.push(seri);
            }
          }
          break;
        case 3:
          this.slickSeries = [];
          result = await this.$http.post("sensordata/range", {
            bid: this.bandinfo.id,
            days: this.dateList,
            dataname: "walk_steps",
          });
          if (this.$session.showConsole) console.log(this.dateList);
          result = result.data.data;

          if (result.length != 0) {
            this.stepTitle = [];
            for (a = 0; a < result.length; a++) {
              seri = [];

              seri.push({ name: "walk", data: result[a].data[0] });
              seri.push({ name: "run", data: result[a].data[1] });
              // console.log(result[a].date);
              this.stepTitle.push(result[a].date);
              this.slickSeries.push(seri);
            }

            if (this.$session.showConsole) console.log(seri);
          }
          break;
      }
      this.$session.load = false;
    },
    typeClick(i) {
      this.typeclick = i;

      this.dateFormatted = this.formatDate(this.date);
      this.slickclick = 0;
      this.graphType = this.typeclick === 3 ? "bar" : "line";
    },
    slickCheck(i) {
      this.slickclick = i;
      for (var j = 0; j < 5; j++) {
        if (j === i) this.slickClick[j].check = true;
        else this.slickClick[j].check = false;
      }
      this.series = this.slickSeries[i];
      this.graphType = this.slickSeries[i][0].name === "walk" ? "bar" : "line";
    },
    slickGraphType(gType) {
      return gType === "walk" ? "bar" : "line";
    },
  },
  watch: {
    async date() {
      this.dateFormatted = this.formatDate(this.date);
    },
  },
};
</script>
<style lang="scss" scoped>
.centered-input >>> input {
  text-align: center;
}
.slick-slider {
  ::v-deep .slick-arrow:before {
    color: #257e53;
    opacity: 1;
  }
  ::v-deep .slick-arrow.slick-disabled:before {
    opacity: 0.25;
  }
}
@media screen and (min-width: 3000px) {
  .history-chart-style {
    width: 700px;
  }
  .detail-text-style {
    font-size: 1.8rem;
  }
  .history-button-style {
    width: 180px;
  }
  .content-text-style {
    font-size: 1.4rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 1904px) {
  .history-chart-style {
    width: 600px;
  }
  .detail-text-style {
    font-size: 1.4rem;
  }
  .history-button-style {
    width: 140px;
  }
  .content-text-style {
    font-size: 1.2rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1903px) {
  .history-chart-style {
    width: 500px;
  }
  .detail-text-style {
    font-size: 1.2rem;
  }
  .history-button-style {
    width: 140px;
  }
  .content-text-style {
    font-size: 1rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
@media screen and (max-width: 749px) {
  .history-chart-style {
    width: 400px;
  }
  .detail-text-style {
    font-size: 1rem;
  }
  .history-button-style {
    width: 120px;
  }
  .content-text-style {
    font-size: 1rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
@media screen and (max-width: 460px) {
  .history-chart-style {
    width: 300px;
  }
  .history-button-style {
    width: 100px;
  }
  .content-text-style {
    font-size: 1rem;
  }
}
</style>
