<template>
  <v-data-table
    class="pa-1"
    falt
    height="24vh"
    :headers="headers"
    :items="getEventList"
    hide-default-footer
    :items-per-page="-1"
  ></v-data-table>
</template>

<script>
export default {
  props: ["eventList"],
  mounted() {
    // let table = document
    //   .querySelector(".v-data-table")
    //   .querySelector(".v-data-table__wrapper")
    //   .querySelector("table")
    //   .querySelector("tbody")
    //   .querySelector("tr");
    // table.style.color = "red";
    // console.log(table);
  },

  computed: {
    getEventList() {
      var el = [];
      for (var i in this.eventList) {
        var type = null;
        var value = "";
        switch (this.eventList[i].type) {
          case 0: {
            type = "낙상발생";
            value = "위험";
            break;
          }
          case 5: {
            type = "심박수이상";
            value = this.eventList[i].value >= 150 ? "위험" : "주의";
            break;
          }
        }
        if (type != null) {
          el.push({
            bid: this.eventList[i].bandid,
            type: type,
            value: value,
          });
        }
      }
      return el;
    },
  },
  data() {
    return {
      headers: [
        {
          text: "번호",
          align: "start",
          sortable: false,
          value: "bid",
        },

        { text: "분류", value: "type" },
        { text: "", value: "value" },
      ],
    };
  },
};
</script>
<style lang="scss"></style>
