import axios from "axios";
import cheerio from "cheerio";
import Vue from "vue";

const restapi = axios.create({ baseURL: "/api/efwb/v1/" });
const sockapi = axios.create({ baseURL: "/sapi/v1" });
const gwapi = axios.create({baseURL: "/api2/"});
const HttpErrorHandler = (MainComponent) => {
  const main = MainComponent;

  const getErrorMessage = (errorCode) => {
    switch (errorCode) {
      case 400:
        return "Invalid Request Parameter";
      case 401:
        return "Error 401";
      case 402:
        return "Error 402";
      case 403:
        return "Error 403";
      case 404:
        return "Error 404";
      case 405:
        return "Error 405";
      case 500:
        return "Error 500";
      default:
        return `Error ${errorCode}`;
    }
  };

  return (error) => {
    if (!error || !error.response) return console.error(error);
    const text = getErrorMessage(error.response.status);
    main.$modal.open({ text }).then(({ modal }) => {
      modal.close();
    });
  };
};
const rest = new Vue({
  methods: {
    async access_history_reload(data) {
      return await restapi.post("access_history/reload", data);
    },
    async logout() {
      return await restapi.post("logout");
    },
    async users_groupinfo(data) {
      return await restapi.get("users/groupinfo/" + data);
    },
    async bands_list() {
      var band = null;
      try {
        band = await restapi.get("bands/list");
      } catch (ex) {
        band = null;
      }
      return band;
    },
    async users_bandinfo(data) {
      return await restapi.get("users/bandinfo/" + data);
    },
    async getWeather() {
      try {
        var result = await axios.get(
          "https://search.naver.com/search.naver?where=nexearch&sm=top_hty&fbm=1&ie=utf8&query=구미 날씨",
          {
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Methods":
                "GET,PUT,POST,DELETE,PATCH,OPTIONS",
            },
          }
        );

        const $ = cheerio.load(result.data);
        console.log($);
      } catch (error) {
        console.error(error);
      }
    },
  },
});
export default { restapi, sockapi, HttpErrorHandler, rest, gwapi };
export { restapi, sockapi, HttpErrorHandler, rest, gwapi };
