<template>
  <v-data-table
    falt
    class="pa-1"
    height="23vh"
    :headers="headers"
    :items="getEventList"
    hide-default-footer
    :items-per-page="-1"
  ></v-data-table>
</template>

<script>
export default {
  props: ["eventList"],
  computed: {
    getEventList() {
      var el = [];

      for (var i in this.eventList) {
        var type = null;
        var value = "";
        switch (this.eventList[i].type) {
          case 1: {
            type = "신호약함";
            value = this.eventList[i].value;
            break;
          }
          case 2: {
            type = "배터리부족";
            value = this.eventList[i].value;
            break;
          }
          case 3: {
            type = "미착용감지";
            break;
          }
          case 4: {
            type = "연결종료";
            break;
          }
        }
        if (type != null) {
          el.push({
            bid: this.eventList[i].bandid,
            type: type,
            value: value,
          });
        }
      }
      return el;
    },
  },
  data() {
    return {
      headers: [
        {
          text: "번호",
          align: "start",
          sortable: false,
          value: "bid",
        },

        { text: "이벤트", value: "type" },
        { text: "", value: "value" },
      ],
    };
  },
};
</script>
<style lang="scss"></style>
