import Vue from "vue";
export default new Vue({
  methods: {
    changeLocalDateFormat(date) {
      return new Date(date).toLocaleDateString();
    },
    changeLocalFormat(date) {
      return new Date(date).toLocaleString();
    },
    changeDateFormat(date) {
      var d = new Date(date);

      return d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
    },
    changeDateKorFormat(date) {
      var d = new Date(date);

      return (
        d.getFullYear() +
        "년 " +
        (d.getMonth() + 1) +
        "월 " +
        d.getDate() +
        "일"
      );
    },

    changeTimeFormat(date) {
      var d = new Date(date);
      return (
        this.change(d.getHours()) +
        ":" +
        this.change(d.getMinutes()) +
        ":" +
        this.change(d.getSeconds())
      );
    },
    changeTimeKorFormat(date) {
      var d = new Date(date);

      return (
        this.change(d.getHours()) +
        ":" +
        this.change(d.getMinutes())
      );
    },
    changeDateSlashFormat(date) {
      let da = new Date(date);
      let now = new Date();
      now = new Date(now);
      let d = new Date(now.setDate(now.getDate() - da));
      return this.change(d.getMonth() + 1) + "/" + this.change(d.getDate());
    },
    changeDateNoneFormat(date) {
      let d = new Date(date);

      return (
        d.getFullYear() +
        "" +
        this.change(d.getMonth() + 1) +
        "" +
        this.change(d.getDate())
      );
    },
    changeFullDateFormat(date) {
      var d = new Date(date);
      return (
        d.getFullYear() +
        "-" +
        this.change(d.getMonth() + 1) +
        "-" +
        this.change(d.getDate()) +
        " " +
        this.change(d.getHours()) +
        ":" +
        this.change(d.getMinutes()) +
        ":" +
        this.change(d.getSeconds())
      );
    },
    changeAmPm(date) {
      var d = new Date(date);
      return d.getHours() < 12 ? "오전" : "오후";
    },
    change(data) {
      return data < 10 ? "0" + String(data) : String(data);
    },
  },
  data() {
    return {
      KR_TIME_DIFF: 9 * 60 * 60 * 1000,
    };
  },
});
