import Vue from 'vue'
import SocketManager from './SocketManager'
import { restapi, sockapi } from './http'

export default new Vue({
  data() {
    return {
      user: null,
      group: '',
      groupstr: '',
      selectBand: null,
      bandinfo: [],
      socket: null,
      data: null,
      load: false,
      showConsole: false,
      bands: {},
      inactiveCheckInterval: null,
    }
  },
  methods: {
    setSession(user, skipStore) {
      this.user = user
      restapi.defaults.headers.common['token'] = user.token
      sockapi.defaults.headers.common['token'] = user.token

      if (!skipStore) localStorage.setItem('SESSION', JSON.stringify(user))

      // 소켓 매니저 초기화
      SocketManager.connect(user.token)

      SocketManager.on('efwbsync', (data) => {
        // console.log('Sync received - raw data:', data)
        try {
          this.onDataHandler(data)
        } catch (error) {
          console.error('Error handling sync data:', error)
        }
      })

      SocketManager.on('efwbasync', (data) => {
        // console.log('Received async data:', data)
        if (data) {
          this.onEventHandler(data)
        }
      })

      SocketManager.on('ehg4_gps', (data) => {
        // console.log('Received GPS data:', data)
        if (data) {
          this.onGPSDataHandler(data)
        }
      })

      // this.socket = io('/receiver', {
      //   query: { token: user.token },
      //   transports: ['websocket', 'polling'],
      //   upgrade: true,
      //   reconnection: true,
      //   reconnectionDelay: 1000,
      //   reconnectionDelayMax: 5000,
      //   timeout: 20000,
      // })

      // // 모든 이벤트 로깅
      // this.socket.onAny((event, ...args) => {
      //   console.log('Received event:', event)
      //   console.log('Event data:', args)
      // })

      // // 비즈니스 이벤트 리스너
      // this.socket.on('efwbsync', (data) => {
      //   // console.log('Sync received - raw data:', data)
      //   try {
      //     this.onDataHandler(data)
      //   } catch (error) {
      //     console.error('Error handling sync data:', error)
      //   }
      // })

      // this.socket.on('efwbasync', (data) => {
      //   // console.log('Received async data:', data)
      //   if (data) {
      //     this.onEventHandler(data)
      //   }
      // })

      // this.socket.on('ehg4_gps', (data) => {
      //   // console.log('Received GPS data:', data)
      //   if (data) {
      //     this.onGPSDataHandler(data)
      //   }
      // })

      // this.socket.on('gateway_connect', (data) => {
      //   // console.log('Received gateway connection:', data)
      //   if (data) {
      //     this.onBandNumHandler(data)
      //   }
      // })

      // 밴드 연결 상태 갱신 옵저버
      this.startInactiveBandChecker()
    },

    // 연결 상태 체크 메서드 추가
    // checkConnectionStatus() {
    //   setInterval(() => {
    //     if (!this.socket.connected) {
    //       console.log('Checking connection status - Disconnected')
    //       this.socket.connect()
    //     }
    //   }, 5000) // 5초마다 체크
    // },

    // New DataHandler for eHG4 GPS
    onGPSDataHandler(data) {
      this.$emit('gps', data)
      // console.log('GPS data received:', data)
    },

    onDataHandler(data) {
      // console.log('session band data = ', data)
      if (data && data.extAddress) {
        const bandId = data.extAddress.high
        const updatedData = {
          ...data,
          active: 'true',
          lastPing: Date.now(),
        }

        this.updateBandStatus(bandId, updatedData)
      } else {
        console.log('Invalid or missing band data', data)
      }
    },

    // 새로 추가: 밴드 상태 업데이트 메서드
    updateBandStatus(bandId, data) {
      Vue.set(this.bands, bandId, {
        ...this.bands[bandId],
        ...data,
      })
      this.$emit('data', data)
    },

    startInactiveBandChecker() {
      if (this.inactiveCheckInterval) {
        clearInterval(this.inactiveCheckInterval)
      }
      this.inactiveCheckInterval = setInterval(() => {
        this.checkInactiveBands()
      }, 3000)
    },

    checkInactiveBands() {
      const now = Date.now()
      Object.keys(this.bands).forEach((bandId) => {
        const band = this.bands[bandId]

        if (band.lastPing < now - 150000) {
          const updatedData = {
            ...band,
            active: 'false',
          }
          Vue.set(this.bands, bandId, updatedData)
          this.$emit('data', updatedData)
        }
      })
    },

    async loadSessionData() {
      try {
        let str = localStorage.getItem('SESSION')
        if (!str) {
          return false
        } else {
          try {
            var res = await restapi.post('access_history/reload', JSON.parse(str))
            if (res.data === '') {
              this.releaseSession()
              return false
            }
          } catch {
            return false
          }
        }
        let json = JSON.parse(str)
        this.user = json
        this.setSession(json, true)
        return true
      } catch (error) {
        console.error(error)
        localStorage.removeItem('SESSION')
        return false
      }
    },
    async historyCheck() {
      var session = localStorage.getItem('SESSION')
      if (session != null) {
        var res = await restapi.post('access_history/reload', JSON.parse(session))
        if (res.data === '') {
          this.releaseSession()
          return false
        } else {
          return true
        }
      } else return false
    },
    releaseSession() {
      restapi.post('logout')
      restapi.defaults.headers.common['token'] = ''
      delete restapi.defaults.headers.common['token']
      sockapi.defaults.headers.common['token'] = ''
      delete sockapi.defaults.headers.common['token']
      localStorage.removeItem('SESSION')
      this.user = null

      // 기존 소켓 연결 해제
      // if (this.socket) this.socket.close()
      // else console.error('No Socket Instance')

      // 소켓 연결 해제
      try {
        SocketManager.disconnect()
      } catch (error) {
        console.error('Error disconnecting socket:', error)
      }
    },
    onErrorHandler(err) {
      switch (err) {
        case 'unauthorized': {
          // TODO: 에러 핸들링
          this.releaseSession()
          break
        }
        case 'xhr poll error': {
          break
        }

        // default: {

        // }
      }
    },

    onEventHandler(data) {
      this.$emit('efwbasync', data)
    },
    onBandNumHandler(data) {
      this.$emit('gateway_connect', data)
    },
    onGatewayDisconnectHandler(data) {
      var gateway = {
        panid: data,
        bandnum: 0,
        connectstate: false,
      }
      this.$emit('gateway_connect', gateway)
    },
  },
  created() {
    // localStorage.setItem(
    //   'SESSION',
    //   JSON.stringify({
    //     created: 'Tue, 09 May 2023 19:12:42 GMT',
    //     id: 1,
    //     name: 'dtriple',
    //     permission: 0,
    //     token: 'a59ea3f078016024f7b60020c447217e27afea4e',
    //     uid: 1000,
    //     username: 'admin',
    //   })
    // )
    this.loadSessionData()
  },
})
