<template>
  <div>
    <v-row>
      <v-col class="mt-4" cols="12" md="12">
        <v-card>
          <div class="ml-4 pt-2 header-text-style font-weight-bold">위치 정보</div>
          <div style="width: 100%; height: 45vh" id="kakaoMap" />
          <v-btn
            class="map-control"
            fab
            small
            dark
            color="white"
            @click="moveToCurrentLocation"
            style="position: absolute; bottom: 20px; right: 20px; z-index: 1;"
          >
            <v-icon color="black">mdi-crosshairs-gps</v-icon>
          </v-btn>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: 'KakaoMap',
  props: {
    bandinfo: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      map: null,
      marker: null,
      infowindow: null,
      gpsData: null,
      isLoading: true,
      // 초기 중심좌표 (서울시청)
      center: {
        lat: 37.566826,
        lng: 126.978656,
      },
    }
  },

  created() {
    this.$session.$on('gps', this.handleGPSData)
  },

  mounted() {
    this.loadKakaoMapScript()
  },

  beforeDestroy() {
    this.$session.$off('gps', this.handleGPSData)
    if (this.infowindow) {
      this.infowindow.close()
    }
    if (this.marker) {
      this.marker.setMap(null)
    }
    this.map = null
  },

  methods: {
    loadKakaoMapScript() {
      if (window.kakao && window.kakao.maps) {
        this.initMap()
        return
      }

      const script = document.createElement('script')

      script.src = `//dapi.kakao.com/v2/maps/sdk.js?appkey=${process.env.VUE_APP_KAKAO_MAPS_API_KEY}&autoload=false`
      script.async = true

      script.onload = () => {
        window.kakao.maps.load(() => {
          this.initMap()
        })
      }

      document.head.appendChild(script)
    },

    moveToCurrentLocation() {
      if (this.marker && this.map) {
        const position = this.marker.getPosition()
        this.map.setCenter(position)
        this.map.setLevel(3)
      }
    },

    initMap() {
      const mapContainer = document.getElementById('kakaoMap')
      const mapOptions = {
        center: new window.kakao.maps.LatLng(this.center.lat, this.center.lng),
        level: 3,
      }

      this.map = new window.kakao.maps.Map(mapContainer, mapOptions)

      // 지도 컨트롤 추가
      const zoomControl = new window.kakao.maps.ZoomControl()
      const mapTypeControl = new window.kakao.maps.MapTypeControl()

      this.map.addControl(zoomControl, window.kakao.maps.ControlPosition.RIGHT)
      this.map.addControl(mapTypeControl, window.kakao.maps.ControlPosition.TOPRIGHT)
    },

    handleGPSData(data) {
      if (!this.$session.user) {
        return
      }

      // 수신된 GPS 데이터의 bid와 props로 전달받은 bandId를 비교
      if (data.bid === this.bandinfo.bid) {
        this.gpsData = data
        this.updateMapPosition()
      }
    },

    updateMapPosition() {
      if (!this.map || !this.gpsData) {
        return
      }

      const position = new window.kakao.maps.LatLng(this.gpsData.latitude, this.gpsData.longitude)

      // 기존 마커가 있다면 제거
      if (this.marker) {
        this.marker.setMap(null)
      }
      if (this.infowindow) {
        this.infowindow.close()
      }

      // 새 마커 생성
      this.marker = new window.kakao.maps.Marker({
        position: position,
        map: this.map,
      })

      // 인포윈도우 생성 및 표시
      const iwContent = `
          <div style="padding:5px;font-size:12px;">
            <strong>${this.bandinfo.name}</strong><br>
            <span>위도: ${this.gpsData.latitude.toFixed(6)}</span><br>
            <span>경도: ${this.gpsData.longitude.toFixed(6)}</span><br>
            <span style="color: #888;">최근 업데이트: ${new Date().toLocaleTimeString()}</span>
          </div>
        `

      this.infowindow = new window.kakao.maps.InfoWindow({
        position: position,
        content: iwContent,
        removable: true,
      })

      this.infowindow.open(this.map, this.marker)

      // 지도 중심 이동
      this.map.setCenter(position)
    },
  },
}
</script>

<style scoped>
@media screen and (min-width: 3000px) {
  .header-text-style {
    font-size: 1.8rem;
  }
}

@media screen and (min-width: 1904px) {
  .header-text-style {
    font-size: 1.4rem;
  }
}

@media screen and (max-width: 1903px) {
  .header-text-style {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 749px) {
  .header-text-style {
    font-size: 1rem;
  }
}
</style>
