<template>
  <div class="text-center">
    <v-dialog v-model="dialog" persistent width="500">
      <v-card>
        <v-card-title
          class="text-h6 font-weight-bold"
          :class="value.state === 0 ? 'red' : 'grey'"
        >
          {{ value.title }}
        </v-card-title>
        <v-card-subtitle class="mt-4 font-weight-bold">
          {{ value.subtitle }}
        </v-card-subtitle>
        <v-card-text>
          {{ value.content }}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="black" text @click="dialogEvent()">
            확인
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  props: ["dialogCheck", "value"],
  watch: {
    dialogCheck(newVal) {
      // console.log(newVal + " " + this.dialog);
      this.dialog = newVal;
    },
  },
  mounted() {},
  methods: {
    dialogEvent() {
      this.$emit("dialog-event", false);
    },
  },
  data() {
    return {
      dialog: false,
    };
  },
};
</script>
