<template>
  <div>
    <v-row>
      <v-col cols="12" md="3">
        <v-card>
          <v-toolbar flat>
            <v-card-subtitle class="black--text font-weight-bold mt-n2 ml-n4 detail-text-style">
              밴드 정보
            </v-card-subtitle>
            <v-spacer></v-spacer>
            <v-btn class="mt-n2 font-weight-medium detail-text-style" text color="point2" depressed>수정 </v-btn>
          </v-toolbar>

          <v-list class="mt-n8">
            <v-list-item>
              <v-list-item-content class="content-text-style">
                이름
              </v-list-item-content>
              <v-list-item-action class="content-text-style">
                {{ bandinfo.name }}
              </v-list-item-action>
            </v-list-item>
            <v-list-item>
              <v-list-item-content class="content-text-style">
                생년월일
              </v-list-item-content>
              <v-list-item-action class="content-text-style" v-text="$date.changeLocalDateFormat(bandinfo.birth)">
              </v-list-item-action>
            </v-list-item>
            <v-list-item>
              <v-list-item-content class="content-text-style">
                성별
              </v-list-item-content>
              <v-list-item-action
                class="content-text-style"
                v-text="bandinfo.gender === null ? '' : bandinfo.gender === 0 ? '남자' : '여자'"
              >
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>
      <v-col cols="12" md="3">
        <v-card>
          <v-card-subtitle class="black--text font-weight-bold detail-text-style">
            심박수 (BPM)
          </v-card-subtitle>
          <v-card-title class="mt-n8 content-text-style" v-text="banddata.bandData.hr"> </v-card-title>
          <apexchart type="line" ref="hrchart" class="mt-n8" :options="hroptions" :series="hrseries"></apexchart>
        </v-card>
      </v-col>
      <v-col cols="12" md="3">
        <v-card>
          <v-card-subtitle class="black--text font-weight-bold detail-text-style">
            산소포화도 (%)
          </v-card-subtitle>
          <v-card-title
            class="mt-n8 content-text-style"
            v-text="banddata.bandData.spo2 != '-' ? banddata.bandData.spo2 / 10 : banddata.bandData.spo2"
          >
          </v-card-title>
          <apexchart type="line" ref="spo2chart" class="mt-n8" :options="spo2options" :series="spo2series"></apexchart>
        </v-card>
      </v-col>
      <v-col cols="12" md="3">
        <v-card>
          <v-card-subtitle class="black--text font-weight-bold detail-text-style">
            걷기 / 달리기
          </v-card-subtitle>
          <v-card-title class="mt-n8 content-text-style" v-text="activity"> </v-card-title>
          <!-- <apexchart
            width="275"
            class="mt-n8"
            :options="options"
            :series="hrseries"
          ></apexchart> -->
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="6">
        <v-card>
          <v-card-subtitle class="black--text font-weight-bold detail-text-style">
            알림
          </v-card-subtitle>
          <v-data-table falt :headers="headers" :items="eventList" :items-per-page="3" class=" mx-4"></v-data-table>
        </v-card>
      </v-col>
      <v-col cols="12" md="6">
        <v-card>
          <v-card-subtitle class="black--text font-weight-bold detail-text-style">
            상태
          </v-card-subtitle>
          <div class="mx-4">
            <v-row class="text-center">
              <v-col>
                <v-card class="mb-n2" flat>
                  <div v-if="banddata.active === 'false'" class="title">
                    -
                  </div>
                  <v-icon
                    v-else
                    class=""
                    size="32
                  "
                    >{{ changeIcon(0, banddata.bandData.scdState) }}</v-icon
                  >
                  <v-card-subtitle class="content-text-style">
                    착용
                  </v-card-subtitle>
                </v-card>
              </v-col>
              <v-col>
                <v-card class="mb-n2" flat>
                  <div class="title">{{ banddata.bandData.battery_level }}</div>
                  <v-card-subtitle class="content-text-style ">
                    배터리 (%)
                  </v-card-subtitle>
                </v-card>
              </v-col>
              <v-col>
                <v-card class="mb-n2" flat>
                  <div class="title">
                    {{ banddata.rssi }}
                  </div>
                  <v-card-subtitle class="content-text-style">
                    수신감도
                  </v-card-subtitle>
                </v-card>
              </v-col>
            </v-row>
            <v-row class="text-center">
              <v-col>
                <v-card class="mb-n2" flat>
                  <div v-if="banddata.active === 'false'" class="title">
                    {{ banddata.bandData.activity }}
                  </div>
                  <v-icon
                    v-else
                    class=""
                    size="32
                  "
                    >{{ changeIcon(1, banddata.bandData.activity) }}</v-icon
                  >

                  <v-card-subtitle class="content-text-style">
                    활동
                  </v-card-subtitle>
                </v-card>
              </v-col>
              <v-col>
                <v-card class="mb-n2" flat>
                  <div class="title">{{ getAirPressure }}</div>
                  <v-card-subtitle class="content-text-style">
                    고도 (m)
                  </v-card-subtitle>
                </v-card>
              </v-col>
              <v-col>
                <v-card class="mb-n2" flat>
                  <div class="title">
                    {{ fall_detect }}
                  </div>
                  <v-card-subtitle class="content-text-style">
                    낙상
                  </v-card-subtitle>
                </v-card>
              </v-col>
            </v-row>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
var hrDataArray = []
var spo2DataArray = []
export default {
  props: ['banddata', 'bandinfo', 'alarmList'],
  watch: {
    banddata() {
      this.onDataHandler()
    },
    async alarmList() {
      this.getEventList()
    },
  },
  created() {},
  computed: {
    getAirPressure() {
      // let altitude = 1012.7
      // let p = Number(this.banddata.bandData.h) / (altitude * 100)
      // let b = 1 / 5.255
      // let alt = 44330 * (1 - p ** b)

      // return alt.toFixed(2)
      return this.banddata.bandData.h
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== 'Name')
    },
  },
  async mounted() {
    this.onDataHandler()
    this.$session.load = true
    hrDataArray = []
    spo2DataArray = []
    this.getEventList()
    await this.getFallData()
    this.$session.load = false
  },
  methods: {
    onDataHandler() {
      if (this.banddata.active === 'true') {
        this.getNewSeries(this.banddata.bandData.hr, this.banddata.bandData.spo2)
        this.activity = this.banddata.bandData.walk_steps + ' / ' + this.banddata.bandData.run_steps
      }
    },
    getEventList() {
      this.eventList = []
      var num = 0
      for (var i = this.alarmList.length - 1; i >= 0; i--) {
        if (this.$date.changeDateFormat(this.alarmList[i].datetime) == this.$date.changeDateFormat(new Date())) {
          this.eventList.push({
            id: ++num,
            type: this.alarmList[i].type,
            value: this.alarmList[i].value,
            datetime: this.$date.changeTimeFormat(this.alarmList[i].datetime),
          })
        }
      }
    },
    async getFallData() {
      var res = await this.$http.post('events/fall_detect', {
        bid: this.bandinfo.id,
        days: [this.$date.changeDateFormat(new Date())],
      })

      res = res.data.data
      if (res.length != 0) {
        this.fall_detect = res[0].data
      }
      res = await this.$http.post('sensordata/activity/oneday', {
        bid: this.bandinfo.id,
        days: [this.$date.changeDateFormat(new Date())],
      })
      res = res.data.data
      if (res.length != 0) {
        this.activity = res[0].walk_steps + ' / ' + res[0].run_steps
      }
      return true
    },
    getNewSeries(hr, spo2) {
      const curr = new Date()
      //if (yrange < 0) start = lastDate;
      hrDataArray.push({
        x: curr,
        y: hr,
      })
      spo2DataArray.push({
        x: curr,
        y: spo2 / 10,
      })
      this.lastDate = curr
      this.realChart()
    },
    realChart() {
      try {
        var me = this
        me.$refs.hrchart.updateSeries([
          {
            data: hrDataArray,
          },
        ])
        me.$refs.spo2chart.updateSeries([
          {
            data: spo2DataArray,
          },
        ])
      } catch (e) {
        window.clearInterval(null)
      }
    },

    changeIcon(type, value) {
      switch (type) {
        case 0: {
          if (value === 2 || value === 3) return 'mdi-check-bold'
          else return 'mdi-close-thick'
        }
        case 1: {
          if (value === 0 || value === 1) return 'mdi-human-handsdown'
          else if (value === 2) return 'mdi-walk'
          else if (value === 3) return 'mdi-run'
          else if (value === 4) return 'mdi-bike'
          else if (value === 5) return 'mdi-run-fast'
        }
      }
    },
  },
  data() {
    return {
      activity: '-',
      restapi: false,
      lastDate: 0,
      eventList: [],
      fall_detect: '-',
      KR_TIME_DIFF: 9 * 60 * 60 * 1000,
      headers: [
        {
          text: '번호',
          align: 'start',
          sortable: false,
          value: 'id',
        },

        { text: '타입', value: 'type' },
        { text: '시간', value: 'datetime' },
        { text: '값', value: 'value' },
      ],

      hroptions: {
        chart: {
          id: 'realtime',
          type: 'line',
          animations: {
            enabled: true,
            easing: 'linear',
            dynamicAnimation: {
              speed: 1000,
            },
          },
          toolbar: {
            show: false,
          },
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          width: 2,
        },
        colors: ['#257E53'],
        xaxis: {
          labels: {
            show: false,
          },
        },
        yaxis: {
          show: true,
          labels: {
            show: true,
          },
        },

        grid: {
          borderColor: '#90A4AE',
          xaxis: {
            lines: {
              show: false,
            },
          },
          yaxis: {
            lines: {
              show: false,
            },
          },
        },
      },
      spo2options: {
        chart: {
          id: 'realtime',
          type: 'line',
          animations: {
            enabled: true,
            easing: 'linear',
            dynamicAnimation: {
              speed: 1000,
            },
          },
          toolbar: {
            show: false,
          },
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          width: 2,
        },
        colors: ['#257E53'],
        xaxis: {
          labels: {
            show: false,
          },
        },
        yaxis: {
          show: true,
          labels: {
            show: true,
          },
        },

        grid: {
          borderColor: '#90A4AE',
          xaxis: {
            lines: {
              show: false,
            },
          },
          yaxis: {
            lines: {
              show: false,
            },
          },
        },
      },
      hrseries: [
        {
          data: hrDataArray.slice(),
        },
      ],
      spo2series: [
        {
          data: spo2DataArray.slice(),
        },
      ],

      fallDetect: '-',
    }
  },
}
</script>
<style lang="scss">
// #nav {
//   padding: 30px;

//   a {
//     font-weight: bold;
//     color: #2c3e50;
//   }
// }

.v-icon-vertical-center {
  position: relative;
  margin: 20px 0;
  display: inline-block;
}

.main {
  display: table;
}

.inner {
  border: 1px solid #000000;
  display: table-cell;
  vertical-align: top;
}
.col-height {
  height: 180px;
}
@media screen and (min-width: 3000px) {
  .detail-text-style {
    font-size: 1.8rem;
  }
  .content-text-style {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > .v-data-table-header > tr > th {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 1904px) {
  .detail-text-style {
    font-size: 1.4rem;
  }
  .content-text-style {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > .v-data-table-header > tr > th {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1903px) {
  .detail-text-style {
    font-size: 1.2rem;
  }
  .content-text-style {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > .v-data-table-header > tr > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
@media screen and (max-width: 749px) {
  .detail-text-style {
    font-size: 1rem;
  }
  .content-text-style {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > .v-data-table-header > tr > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
</style>
