<template align="center">
  <div v-if="loading">
    <span class="title">
      {{ $date.changeDateKorFormat(nowDate) }}
    </span>
    <v-row align="center">
      <v-col>
        <!-- <span class="title">{{ $date.changeAmPm(nowDate) }} </span> -->
        <span class="display-3 font-weight-bold">
          {{ $date.changeTimeKorFormat(nowDate) }}
        </span>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  created() {
    setInterval(() => this.getNowDate(), 1000);
  },
  methods: {
    getNowDate() {
      this.loading = true;
      this.nowDate = new Date();
    },
  },
  data() {
    return {
      nowDate: null,
      loading: false,
    };
  },
};
</script>
