<template>
  <div>
    <v-text-field
      v-model="search"
      outlined
      label="Search"
      color="point2"
      append-icon="mdi-magnify"
      class="node-text-style"
      style="width:300px; "
    ></v-text-field>
    <v-data-iterator
      :items="gatewayinfo"
      :search="search"
      :no-data-text="nodata"
      :page.sync="page"
      :items-per-page.sync="itemsPerPage"
      hide-default-footer
    >
      <template v-slot:default="props">
        <v-container fluid class="mt-n2">
          <v-row>
            <v-col
              v-for="(gateway, n) in props.items"
              :key="n"
              cols="12"
              md="3"
            >
              <v-card flat class="mb-1">
                <v-btn
                  block
                  :color="gateway.connect_state === true ? 'thirdly' : 'grey'"
                  height="60"
                  @click="
                    $router.push({
                      path: 'gateway/detail',
                      query: { gatewayinfo: JSON.stringify(gateway) },
                    })
                  "
                  class="node-text-style"
                >
                  {{ gateway.pid }}&nbsp; &nbsp;{{ gateway.alias }}</v-btn
                >
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </template>
      <template v-slot:footer>
        <v-row class="" align="center" justify="center">
          <v-spacer></v-spacer>

          <span
            class="mr-4
            grey--text"
          >
            Page {{ page }} of {{ numberOfPages }}
          </span>

          <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

          <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
        </v-row>
      </template>
    </v-data-iterator>
  </div>
</template>
<script>
export default {
  props: ["gatewayinfo"],
  data: () => ({
    num: 74,
    search: "",
    nodata: "",
    page: 1,
    itemsPerPage: 28,
  }),
  methods: {
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.gatewayinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.hidden {
  visibility: hidden;
}
@media screen and (min-width: 3000px) {
  .node-text-style {
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1904px) {
  .node-text-style {
    font-size: 1.4rem;
  }
}
@media screen and (max-width: 1903px) {
  .node-text-style {
    font-size: 1.2rem;
  }
}
</style>
