<template>
  <div>
    <v-toolbar flat class="primary">
      <v-toolbar-title
        class="text-uppercase font-weight-bold detail-header-text"
      >
        게이트웨이 정보
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-list class="primary">
        <v-list-item>
          <v-list-item-content style="text-align: right">
            <v-list-item-title class="detail-sub-text">
              {{ gatewayinfo.pid }}&nbsp; {{ gatewayinfo.alias }}
            </v-list-item-title>
            <v-list-item-subtitle
              class="detail-sub-text "
              :class="
                gatewayinfo.connect_state === false
                  ? 'red--text'
                  : 'green--text'
              "
              v-text="
                gatewayinfo.connect_state === false ? '연결 종료' : '연결'
              "
              >연결 중</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-toolbar>
    <div class="mx-4 mt-6">
      <v-divider></v-divider>
      <div class="mt-6">
        <v-row>
          <v-col cols="12" md="4">
            <v-card>
              <v-toolbar flat>
                <v-card-subtitle
                  class="black--text font-weight-bold mt-n2 ml-n4 detail-text-style"
                >
                  정보
                </v-card-subtitle>
                <v-spacer></v-spacer>
                <v-btn
                  class="mt-n2 font-weight-medium content-text-style"
                  text
                  color="point2"
                  depressed
                  >수정
                </v-btn>
              </v-toolbar>

              <v-list class="mt-n4">
                <v-list-item>
                  <v-list-item-content class="content-text-style">
                    별칭
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    {{ gatewayinfo.alias }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content class="content-text-style">
                    팬 아이디
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    {{ gatewayinfo.pid }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content class="content-text-style">
                    아이피 주소
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    {{ gatewayinfo.ip }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content class="content-text-style">
                    네트워크 모드
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    Beacon Enabled
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content class="content-text-style">
                    마지막 연결 시간
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    {{
                      $date.changeFullDateFormat(
                        new Date(gatewayinfo.connect_time) - $date.KR_TIME_DIFF
                      )
                    }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content class="content-text-style">
                    마지막 연결 종료 시간
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    {{
                      $date.changeFullDateFormat(
                        new Date(gatewayinfo.disconnect_time) -
                          $date.KR_TIME_DIFF
                      )
                    }}
                  </v-list-item-action>
                </v-list-item>
                <v-list-item class="content-text-style">
                  <v-list-item-content>
                    생성일
                  </v-list-item-content>
                  <v-list-item-action class="content-text-style">
                    {{ $date.changeLocalDateFormat(gatewayinfo.created) }}
                  </v-list-item-action>
                </v-list-item>
              </v-list>
            </v-card></v-col
          >
          <v-col cols="12" md="8">
            <v-card>
              <v-toolbar class="mb-n4" flat>
                <v-toolbar-title
                  class="detail-text-style black--text font-weight-bold"
                  >밴드</v-toolbar-title
                >

                <v-spacer></v-spacer>
                <v-btn depressed color="white">
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </v-toolbar>
              <v-data-table
                class="mt-2"
                falt
                :headers="headers"
                :items="bandList"
                :items-per-page="5"
              >
                <template v-slot:item.actions="{ item }">
                  <v-icon @click="deleteItem(item)" :size="setIconSize()">
                    mdi-delete
                  </v-icon>
                </template>
              </v-data-table>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  async mounted() {
    this.gatewayinfo = JSON.parse(this.$route.query.gatewayinfo);
    this.bandList = await this.getBandList();
  },
  methods: {
    async getBandList() {
      var bandlist = null;
      try {
        var res = await this.$http.get(
          "gateways/bandinfo/" + this.gatewayinfo.id
        );
        bandlist = res.data.data;
        for (var i = 0; i < bandlist.length; i++) {
          bandlist[i].num = i + 1;
        }
      } catch (ex) {
        bandlist = null;
      }
      return bandlist;
    },
    setIconSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 20;
        case "sm":
          return 22;
        case "md":
          return 24;
        case "lg":
          return 28;
        case "xl":
          return 30;
      }
    },
  },
  data() {
    return {
      restapi: false,
      gatewayinfo: { pid: "", alias: "" },
      headers: [
        {
          text: "번호",
          align: "start",
          sortable: true,
          value: "num",
        },
        { text: "밴드 아이디", value: "bid", sortable: true },
        { text: "별칭", value: "alias", sortable: true },
        { text: "이름", value: "name", sortable: true },
        { text: "", value: "actions", sortable: false },
      ],
      bandList: [
        { id: 1, bid: "0x00", alias: "D1", name: "하재경" },
        { id: 2, bid: "0x00", alias: "D1", name: "하재경" },
        { id: 3, bid: "0x00", alias: "D1", name: "하재경" },
      ],
    };
  },
};
</script>
<style lang="scss" scoped>
.centered-input >>> input {
  text-align: center;
}
.slick-slider {
  ::v-deep .slick-arrow:before {
    color: #257e53;
    opacity: 1;
  }
  ::v-deep .slick-arrow.slick-disabled:before {
    opacity: 0.25;
  }
}
@media screen and (min-width: 3000px) {
  .detail-text-style {
    font-size: 1.8rem;
  }
  .content-text-style {
    font-size: 1.4rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 1904px) {
  .detail-text-style {
    font-size: 1.4rem;
  }
  .content-text-style {
    font-size: 1.2rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1903px) {
  .detail-text-style {
    font-size: 1.2rem;
  }
  .content-text-style {
    font-size: 1rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
@media screen and (max-width: 749px) {
  .detail-text-style {
    font-size: 1rem;
  }
  .content-text-style {
    font-size: 1rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
</style>
