<template>
  <div>
    <v-toolbar flat class="primary">
      <v-spacer></v-spacer>
      <v-img
        class="switch_mode_image"
        :src="
          mode ? require('../../assets/image/switch_list_btn.svg') : require('../../assets/image/switch_node_btn.svg')
        "
        max-height="60"
        max-width="120"
        @click="mode = !mode"
      ></v-img>
    </v-toolbar>
    <band-list :bandinfo="bandinfo" @move-detail="moveBandDetail" v-if="mode" />
    <band-node v-else-if="!mode" :bandinfo="bandinfo" @move-detail="moveBandDetail" />
  </div>
</template>
<script>
import BandList from './BandList.vue'
import BandNode from './BandNode.vue'
export default {
  props: ['band_data'],
  watch: {
    band_data() {
      this.onDataHandler(this.band_data)
    },
  },
  components: {
    BandList,
    BandNode,
  },
  data: () => ({
    mode: true,
    bandinfo: [],
    gpsData: {},
    restapi: false,
    data: null,
    bandvalue: {
      bandData: {
        start_byte: '',
        sample_count: '',
        fall_detect: '',
        battery_level: '',
        hrConfidence: '',
        spo2Confidence: '',
        hr: '',
        spo2: '',
        motionFlag: '',
        scdState: '',
        activity: '',
        walk_steps: '',
        run_steps: '',
        x: '',
        y: '',
        z: '',
        t: '',
        h: '',
        active: 'false',
      },
      rssi: '',
    },
  }),

  async mounted() {
    this.$session.load = true
    if (this.$session.user != null) {
      await this.getBandList()
    }
    this.$session.load = false
    // this.$session.$on('gps', async (data) => {
    //   this.gpsData = data
    //   // console.log(data)
    //   if (this.$session.user != null) {
    //     this.handleGPSData(this.gpsData)
    //   }
    // })
  },
  methods: {
    async getBandList() {
      try {
        let bands = null
        bands = await this.$http.post('users/bandlist', {
          uid: this.$session.user.id,
          permission: this.$session.user.permission,
        })

        bands = bands.data.data
        this.restapi = true
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            // DB에서 connect_state 불러와 active 값 초기화를 위한 밴드 상태 복사
            const bandValueCopy = JSON.parse(JSON.stringify(this.bandvalue))
            bandValueCopy.bandData.active = bands[i].connect_state === 0 ? 'false' : 'true'

            this.bandinfo.push({
              num: i + 1,
              id: bands[i].id,
              bid: bands[i].bid,
              alias: bands[i].alias,
              name: bands[i].name,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              connect_state: bands[i].connect_state,
              disconnect_time: bands[i].disconnect_time,
              connect_time: bands[i].connect_time,
              bandvalue: bandValueCopy,
              gpsData: null,
            })
          }
        }
        // console.log('bands= ', bands)
        // console.log('bandinfo= ', this.bandinfo)
      } catch (error) {
        console.error('밴드 연결 상태 업데이트 중 오류 발생:', error)
      }
    },

    onDataHandler(band_data) {
      if (this.$session.showConsole) console.log(band_data)
      var index = this.bandinfo
        .map(function(d) {
          return d['bid']
        })
        .indexOf(band_data.extAddress.high)

      if (index !== -1) {
        switch (band_data.active) {
          case 'false':
            this.bandinfo[index].bandvalue = this.bandvalue
            break
          case 'true':
            this.bandinfo[index].bandvalue = band_data
            break
        }
      }
    },

    moveBandDetail(band) {
      const bandInfo = {
        num: band.num,
        id: band.id,
        bid: band.bid,
        alias: band.alias,
        name: band.name,
        birth: band.birth,
        gender: band.gender,
        created: band.created,
        connect_state: band.connect_state,
        disconnect_time: band.disconnect_time,
        connect_time: band.connect_time,
      }

      this.$router.replace({
        path: 'band/detail',
        query: {
          bandinfo: JSON.stringify(bandInfo),
        },
      })
    },

    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number
    },
  },
}
</script>
