import { restapi, sockapi, HttpErrorHandler, rest,gwapi } from "./http";
import SessionService from "./session";
import Date from "./Date";
import Data from "./Data";
export default {
  install(Main) {
    Main.prototype.$http = restapi;
    Main.prototype.$gwapi = gwapi;
    Main.prototype.$sock = sockapi;
    Main.prototype.$session = SessionService;
    Main.prototype.$httpErrorHandler = HttpErrorHandler(Main);
    Main.prototype.$rest = rest;
    Main.prototype.$date = Date;
    Main.prototype.$dataHandler = Data;
  },
};
