<template>
  <div>
    <div class="ml-4 pt-2 header-text-style font-weight-bold">
      {{ $vuetify.lang.t("$vuetify.efwb.weather") }}
    </div>
    <v-divider class="mt-2" />
    <div v-if="loading">
      <div v-if="result != null">
        <div class="ml-4 pt-2 location-text-style">{{ location }}</div>
        <v-row no-gutters :class="setIconMargin()">
          <v-col cols="4">
            <div class="text-end">
              <v-icon :size="setIconSize()">
                {{ changeIcon(result.status) }}
              </v-icon>
            </div>
          </v-col>
          <v-col cols="8" :class="setContentMargin()">
            <v-row no-gutters :class="setTempRightMargin()">
              <v-col cols="6">
                <div class="temp-text-style text-right mr-4 font-weight-bold">
                  {{ result.temp }}
                </div>
              </v-col>
              <v-col cols="6" class="mt-2">
                <span class="min-max-text-style blue--text">
                  {{ result.min }}
                </span>
                <span>/ </span>
                <span class="min-max-text-style red--text">{{
                  result.max
                }}</span>
              </v-col>
            </v-row>
            <v-row no-gutters >
              <v-col cols="12">
                <div
                  class="dust-text-style text-right "
                  :class="setWeatherMargin()"
                >
                  {{ result.status }} · {{ result.wind }}
                  {{ result.wind_strength }}
                </div>
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col cols="12" class="text-right">
                <span class="dust-text-style"> 미세 </span>
                <span
                  class="dust-text-style font-weight-bold"
                  :class="finedustTextColor(result.finedust, 0)"
                >
                  {{ result.finedust }}
                </span>
                <span class="dust-text-style"> · 초미세 </span>
                <span
                  class="dust-text-style font-weight-bold"
                  :class="finedustTextColor(result.ultrafinedust, 1)"
                >
                  {{ result.ultrafinedust }}
                </span>
              </v-col>
            </v-row>
          </v-col></v-row
        >
        <!-- <v-row no-gutters :class="setTimeMargin()">
          <v-col cols="12" align="center" justify="center">
            <v-row no-gutters class="">
              <v-col v-for="(data, i) in result.forecast" :key="i">
                <div class="time-weather-text-style font-weight-bold">
                  {{ data.value }}
                </div>
              </v-col>
            </v-row>
            <v-row no-gutters :class="setTimeMargin()">
              <v-col v-for="(data, i) in result.forecast" :key="i">
                <div class="time-weather-text-style">
                  {{ data.time }}
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row> -->
      </div>
      <div
        v-else
        class="text-center"
        style="display: flex; align-items: center; justify-content: center"
      >
        날씨 정보를 불러 올 수 없습니다.
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["eventloading"],
  async mounted() {
    await this.getWeather();
  },
  methods: {
    async getWeather() {
      try {
        var result = await this.$http.get("weather/" + this.location);
        result = result.data;

        if (result.temp != 0) {
          this.result = result;
        }
      } finally {
        this.loading = true;
        this.$emit("weather-loading", true);
      }
    },
    changeIcon(status) {
      switch (status) {
        case "맑음":
          return "mdi-white-balance-sunny";
        case "흐리고 가끔 비":
          return "mdi-weather-partly-rainy";
        case "구름많음":
          return "mdi-apple-icloud";
        case "흐리고 비":
          return "mdi-weather-rainy";
        case "흐리고 비/눈":
          return "mdi-weather-partly-snowy-rainy";
        case "흐림":
          return "mdi-apple-icloud";
        case "구름많고 한때 비":
          return "mdi-weather-rainy";
        case "비":
          return "mdi-weather-pouring";
        case "소낙비":
          return "mdi-weather-pouring";
        case "눈비":
          return "mdi-weather-snowy-rainy";
        case "눈":
          return "mdi-weather-snowy";
        case "대체로 흐림":
          return "mdi-apple-icloud";
        case "구름 조금":
          return "mdi-weather-partly-cloudy";
        case "드문드문 안개":
          return "mdi-weather-fog";
        default:
          return "mdi-white-balance-sunny";
      }
    },
    setIconSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 40;
        case "sm":
          return 60;
        case "md":
          return 60;
        case "lg":
          return 80;
        case "xl":
          return 100;
      }
    },
    setContentMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "mt-n2";
        case "sm":
          return "mt-n8";
        case "md":
          return "mt-n2";
        case "lg":
          return "mt-n2";
        case "xl":
          return "mt-n2";
      }
    },
    setTempRightMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "";
        case "sm":
          return "";
        case "md":
          return "mr-16";
        case "lg":
          return "mr-16";
        case "xl":
          return "mr-16";
      }
    },
    setIconMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "";
        case "sm":
          return "";
        case "md":
          return "";
        case "lg":
          return "mt-n4";
        case "xl":
          return "mt-n4";
      }
    },
    setWeatherMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "mr-2 mt-2";
        case "sm":
          return "mr-2 mt-2";
        case "md":
          return "mr-4 mt-2";
        case "lg":
          return "mr-4 mt-2";
        case "xl":
          return "mr-8 mt-2";
      }
    },
    setTimeMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "";
        case "sm":
          return "";
        case "md":
          return "";
        case "lg":
          return "mt-3";
        case "xl":
          return "mt-4";
        default:
          return "mt-6";
      }
    },
    finedustTextColor(text, type) {
      var dustClass = "";
      switch (text) {
        case "좋음":
          dustClass += "blue--text ";
          break;
        case "보통":
          dustClass += "green--text ";
          break;
        case "나쁨":
          dustClass += "orange--text ";
          break;
        case "한때나쁨":
          dustClass += "orange--text ";
          break;
        case "매우나쁨":
          dustClass += "red--text ";
          break;
        default:
          dustClass += "black--text ";
          break;
      }
      if (type === 1) {
        switch (this.$vuetify.breakpoint.name) {
          case "xs":
            dustClass += "mr-2";
            break;
          case "sm":
            dustClass += "mr-2";
            break;
          case "md":
            dustClass += "mr-4";
            break;
          case "lg":
            dustClass += "mr-4";
            break;
          case "xl":
            dustClass += "mr-8";
            break;
        }
      }

      return dustClass;
    },
  },
  data() {
    return {
      loading: false,
      result: null,
      location: "구미",
    };
  },
};
</script>
<style lang="scss">
@media screen and (min-width: 3000px) {
  .header-text-style {
    font-size: 1.6rem;
  }
  .location-text-style {
    font-size: 1.6rem;
  }
  .min-max-text-style {
    font-size: 1.6rem;
  }

  .time-bottom-margin {
    margin-top: 36px;
  }
  .dust-text-style {
    font-size: 1.6rem;
  }

  .temp-text-style {
    font-size: 2.8rem;
  }
  .time-weather-text-style {
    font-size: 1.6rem;
  }
}
@media screen and (max-width: 2999px) {
  .header-text-style {
    font-size: 1.2rem;
  }
  .location-text-style {
    font-size: 1.4rem;
  }
  .time-weather-text-style {
    font-size: 1.4rem;
  }
  .min-max-text-style {
    font-size: 1.4rem;
  }

  .dust-text-style {
    font-size: 1.4rem;
  }

  .temp-text-style {
    font-size: 2.6rem;
  }
}
@media screen and (max-width: 2250px) {
  .header-text-style {
    font-size: 1.2rem;
  }
  .location-text-style {
    font-size: 1.2rem;
  }
  .time-weather-text-style {
    font-size: 1.3rem;
  }
  .min-max-text-style {
    font-size: 1.2rem;
  }

  .dust-text-style {
    font-size: 1.3rem;
  }

  .temp-text-style {
    font-size: 2rem;
  }
}
@media screen and (max-width: 1903px) {
  .header-text-style {
    font-size: 1.1rem;
  }
  .min-max-text-style {
    font-size: 1rem;
  }
  .location-text-style {
    font-size: 1.1rem;
  }
  .time-weather-text-style {
    font-size: 1.1em;
  }

  .dust-text-style {
    font-size: 1.1rem;
  }

  .temp-text-style {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 1293px) {
  .header-text-style {
    font-size: 1rem;
  }
  .min-max-text-style {
    font-size: 1rem;
  }
  .location-text-style {
    font-size: 1rem;
  }
  .time-weather-text-style {
    font-size: 1rem;
  }

  .dust-text-style {
    font-size: 0.9rem;
  }

  .temp-text-style {
    font-size: 1.6rem;
  }
}
</style>
