<template>
  <div>
    <Table :data="userlist" :header="headers" :type="2" />
  </div>
</template>
<script>
import Table from "../Table.vue";
export default {
  components: {
    Table,
  },
  mounted() {
    this.$session.load = true;
    this.getUserList();
    this.$session.load = false;
  },
  data: () => ({
    userlist: [],
    search: "",
    nodata: "",
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
    detail: false,
    group: { groupname: "" },
    headers: [
      {
        text: "ID",
        align: "start",
        sortable: true,
        value: "num",
      },
      {
        text: "User ID",
        value: "uid",
      },
      { text: "Name", value: "name" },
      { text: "Permission", value: "permission" },
      { text: "Group", value: "groupname" },
    ],
  }),
  methods: {
    async getUserList() {
      try {
        var res = [];

        res = await this.$http.get(
          "users/samegroup/" +
            this.$session.user.id +
            "_" +
            this.$session.user.permission
        );
        var groupinfo = await this.$http.post("groups/detail", {
          gid: res.data.data.gid,
        });
        this.group = groupinfo.data.groups;
        this.userlist = res.data.data.userlist;
        for (var i = 0; i < this.userlist.length; i++) {
          this.userlist[i].groupname = this.group.groupname;
          this.userlist[i].num = i + 1;
          switch (this.userlist[i].permission) {
            case 0:
              this.userlist[i].permission = "admin";
              break;
            case 1:
              this.userlist[i].permission = "manager";
              break;
          }
        }
        if (this.$session.showConsole) console.log(this.userlist);
      } catch (ex) {
        this.userlist = [];
      }
    },
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },

    listClick(user) {
      this.$router.replace({
        path: "/user/detail",
        query: {
          user: JSON.stringify(user),
          group: JSON.stringify(this.group),
        },
      });
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(5 / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
