<template>
  <div>
    <v-text-field
      v-model="search"
      outlined
      label="Search"
      color="point2"
      append-icon="mdi-magnify"
      class="node-text-style"
      style="width:300px; "
    ></v-text-field>
    <v-data-iterator
      :items="bandinfo"
      :search="search"
      :no-data-text="nodata"
      :page.sync="page"
      :items-per-page.sync="itemsPerPage"
      hide-default-footer
    >
      <template v-slot:default="props">
        <v-container fluid class="mt-n2">
          <v-row>
            <v-col v-for="(band, n) in props.items" :key="n" cols="12" md="2">
              <v-card class="rounded-lg" @click="$emit('move-detail', band)">
                <v-list
                  justify="center"
                  height="160"
                  :color="band.bandvalue.active === 'true' ? 'thirdly' : 'grey'"
                  ><v-list-item
                    ><v-list-item-content class="mt-n4 "
                      ><v-list-item-title class="node-text-style">{{
                        band.num
                      }}</v-list-item-title></v-list-item-content
                    >
                    <v-list-item-icon class="list-item  mt-n1">
                      <img
                        :src="setScdState(band.bandvalue)"
                        :width="setIconSize()"
                      />
                      <v-icon color="black" :size="setIconSize()">
                        {{ setIcon(band.bandvalue, 0) }}
                      </v-icon>

                      <v-icon color="black" :size="setIconSize()">
                        {{ setIcon(band.bandvalue, 1) }}
                      </v-icon>
                    </v-list-item-icon>
                  </v-list-item>
                  <div class="text-center node-text-style">{{ band.bid }}</div>
                </v-list>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </template>
      <template v-slot:footer>
        <v-row class="" align="center" justify="center">
          <v-spacer></v-spacer>

          <span
            class="mr-4
            grey--text"
          >
            Page {{ page }} of {{ numberOfPages }}
          </span>

          <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

          <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
        </v-row>
      </template>
    </v-data-iterator>
  </div>
</template>

<script>
export default {
  props: ["bandinfo"],
  data: () => ({
    restapi: false,
    num: 75,
    search: "",
    nodata: "",
    page: 1,
    itemsPerPage: 24,
  }),
  methods: {
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    setScdState(bandvalue) {
      if (bandvalue.active === "false")
        return require("../../assets/image/scdState.svg");
      else if (
        bandvalue.bandData.scdState === 3 ||
        bandvalue.bandData.scdState === 2
      )
        return require("../../assets/image/scdState.svg");
      else return require("../../assets/image/noScdState.svg");
    },
    setIconColor(bandvalue) {
      if (bandvalue.active === "false") return "grey";
      else return "black";
    },
    setIcon(bandvalue, type) {
      switch (type) {
        case 0:
          if (bandvalue.active === "false")
            return "mdi-signal-cellular-outline";
          else if (bandvalue.rssi > -30) return "mdi-signal-cellular-3";
          else if (bandvalue.rssi > -50) return " mdi-signal-cellular-2";
          else if (bandvalue.rssi > -75) return "mdi-signal-cellular-1";
          else return "mdi-signal-cellular-outline";

        case 1:
          if (bandvalue.active === "false") return "mdi-battery-outline";
          else if (bandvalue.bandData.battery_level > 80)
            return " mdi-battery-high";
          else if (bandvalue.bandData.battery_level > 40)
            return " mdi-battery-medium";
          else if (bandvalue.bandData.battery_level > 15)
            return "mdi-battery-low";
          else return "mdi-battery-outline";
      }
    },
    setIconSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 28;
        case "sm":
          return 30;
        case "md":
          return 30;
        case "lg":
          return 32;
        case "xl":
          return 34;
      }
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.bandinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.hidden {
  visibility: hidden;
}

@media screen and (min-width: 3000px) {
  .node-text-style {
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1904px) {
  .node-text-style {
    font-size: 1.4rem;
  }
}
@media screen and (max-width: 1903px) {
  .node-text-style {
    font-size: 1.2rem;
  }
}
</style>
