<template>
  <div>
    <Table
      :data="gatewayinfo"
      :header="headers"
      :type="1"
      @move-detail="moveGWDetail"
    />
  </div>
</template>
<script>
import Table from "../Table.vue";
export default {
  components: {
    Table,
  },
  props: ["gatewayinfo"],
  watch: {},
  mounted() {},
  data(vm) {
    return {
      search: "",
      nodata: "",
      KR_TIME_DIFF: 9 * 60 * 60 * 1000,
      itemsPerPageArray: [5, 10, 15, 20],
      page: 1,
      itemsPerPage: 10,
      headers: vm.computeHeader(),
    };
  },
  methods: {
    onDataHandler(data) {
      if (this.$session.showConsole) console.log(data);
      if (data.active === "false") {
        this.bandinfo[data.shortAddress - 1].bandvalue = this.bandvalue;
      } else {
        this.bandinfo[data.shortAddress - 1].bandvalue = data;
      }
    },

    async listClick(bandinfo) {
      //console.log(bandinfo);
      if (await this.$session.historyCheck()) {
        this.$router.replace({
          name: "Band Info",
          query: { bandInfo: JSON.stringify(bandinfo) },
        });
      } else {
        this.$session.releaseSession();
        this.$router.replace("/");
      }
    },
    moveGWDetail(gw) {
      this.$router.push({
        path: "gateway/detail",
        query: { gatewayinfo: JSON.stringify(gw) },
      });
    },
    computeHeader() {
      var result = [
        {
          text: "Status",
          align: "start",
          sortable: true,
          value: "connect_state",
        },
        {
          text: "ID",
          value: "num",
        },
        { text: "Pan ID", value: "pid" },
        { text: "Alias", value: "alias" },
        { text: "Band Num", value: "connectnum" },
        {
          text: "Connect Time",
          value: "connect_time",
          filter: (value) => {
            return this.dateCompute(value);
          },
          sortable: false,
        },
        { text: "Disconnect Time", value: "disconnect_time", sortable: false },
      ];

      return result;
    },
    dateCompute(value) {
      if (this.$session.showConsole)
        console.log(
          this.$date.changeFullDateFormat(
            new Date(value) - this.$date.KR_TIME_DIFF
          )
        );
      return this.$date.changeFullDateFormat(
        new Date(value) - this.$date.KR_TIME_DIFF
      );
    },
  },
  computed: {},
};
</script>
<style lang="scss">
.list-item {
  align-self: center; // [jk]
}
.text-style {
  font-size: 1.2rem;
}
.align-style {
  width: 40px;
}
</style>
